
:root {
	--templatiq-primary-color: #8941FF;
	--templatiq-dark-color: #101014;
	--templatiq-btn-color: #35353D;
	--templatiq-white-color: #ffffff;
	--templatiq-body-color: #57575F;
	--templatiq-gray-color: #6B6B73;
	--templatiq-gray-color2: #D1D1D9;
	--templatiq-sec-color: #F1F1F4;
	--templatiq-section-color: #F8F8F9;
	--templatiq-border-color: #E8E8EE;
	--templatiq-placeholder-color: #94949A;
	--templatiq-success-color: #2AB76E;
	--templatiq-danger-color: #D53034;
	--templatiq-warning-color: #F4A11C;
	--templatiq-pro-color: #F17D0E;
	--templatiq-pro-color2: #D16A0A;

	--templatiq-box-shadow: 0 4px 16px rgba(16, 16, 20, 0.04);
	--templatiq-box-shadow-focus: 0 4px 16px rgba(137, 65, 255, .15);
}

ul {
	margin: 0; 
	padding: 0; 
}
li {
	padding: 0; 
	margin: 0;
}
a { 
	text-decoration: none;
	outline: none;
	box-shadow: none;
	transition: color 0.3s ease;
}
button {
	border: none;
	outline: none;
	box-shadow: none;
}

.templatiq-root {
	margin-inline-start: -20px;
}
